  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  margin: 0.625rem 0 0.625rem 0;
  font-size: 3.6rem;
}

h3 {
  font-weight: 700;
  font-size: 2.6rem;
