}

a:hover {
  cursor: pointer;
}

.alerta {
  width: 100%;
  display: block;
  margin-bottom: 0.5rem;
  padding: 0.5rem 0.5rem 0.8rem 0.8rem;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 120%;
  font-size: 1.6rem;
}

.error {
  background-color: #cb0000;
}

.exito {
  background-color: #329f00;
}

.header {
  padding: 0.5rem 0rem 0.5rem 0rem;
  width: 100%;
  background-color: #e1e1e1;
}

.header__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.header__grid section:nth-child(1) {
  padding: 0.5rem 0rem 0.2rem 0rem;
}

.header__grid section:nth-child(2) {
  padding: 0.2rem 0rem 0.6rem 0rem;
}

.header__grid__secction {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.grid__section__conteiner a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #1a1b15;
  padding: 0.5rem 0.5rem;
}

.header-menu, .header-account, .header-shopcart, .search-bar__icon {
  font-size: 2.88rem;
  font-weight: 500;
}

.header-menu {
  padding: 0.5rem 0.5rem;
}

.header-logo {
  font-size: 1.92rem;
  font-weight: 700;
}

.search-bar__field {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: row;
  align-items: center;
}

.search-bar__text {
  width: 100%;
  height: 100%;
  padding: 0.5rem 3.8rem 0.5rem 1.5rem;
  border-radius: 1.2rem;
}

.search-bar__icon {
  position: absolute;
  right: 0;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  border-radius: 1.2rem;
}

.footer {
  width: 100%;
  background-color: #e1e1e1;
}

.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.footer__grid__section-conteiner {
  display: flex;
  flex-direction: column;
}

.back-init {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(210, 209, 209, 0.768627451);
}

.footer__nav-menu {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  vertical-align: middle;
  padding: 1.5rem 0rem 1.5rem 0rem;
}

.nav-menu__conteiner {
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
}

.nav-menu__conteiner h4 {
  text-align: left;
  padding: 0.2rem 0rem 0.2rem 0rem;
  margin: 0;
}

.nav-menu__conteiner a, .nav-menu__conteiner h4 {
  word-break: break-word;
  padding-left: 0.8rem;
  margin: 0;
}

.footer-copy {
  text-align: center;
  padding: 0.8rem 0rem 4rem 0rem;
}

.fog {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #1a1b15;
  z-index: 1;
}

.menu_conteiner {
  position: absolute;
  width: 70%;
  height: 100%;
  background-color: #FFFFFF;
  top: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
}

.menu_close {
  position: absolute;
  right: 0;
  top: 0;
  color: #FFFFFF;
  font-size: 3rem;
  padding: 2.5rem 2.5rem;
  z-index: 2;
  font-weight: 500;
}

.menu_background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  font-size: 3rem;
  z-index: 1;
}

.menu-header {
  background-color: rgba(210, 209, 209, 0.768627451);
  width: 100%;
  padding: 0.8rem 0rem 0rem 0rem;
}

.menu-header__conteiner {
  display: flex;
  flex-direction: column;
  vertical-align: middle;
}

.menu-header-btn-login, .menu-header-btn-explorar {
  font-size: 2.08rem;
  font-weight: 700;
  padding: 0.8333333333rem 0rem;
}

.menu-header-btn-login {
  text-align: right;
}

.menu-header-btn-explorar {
  text-align: left;
}

.menu-body__conteiner {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  padding: 0.8rem 0rem 0rem 0rem;
  gap: 0.8rem;
}

.menu-body__tendencias, .menu-body__categorias {
  border-top: 0.2rem solid #e1e1e1;
  padding: 0.6rem 0rem;
}

.menu-body__tendencias-head, .menu-body__categorias-head, .menu-body__home-head a {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
}

.menu-body__home h3, .menu-body__tendencias h3, .menu-body__categorias h3 {
  text-align: left;
  vertical-align: middle;
  margin: 0;
