}

.menu-body span {
  font-size: 2.8rem;
  margin-right: 0.2rem;
}

.search-viewport {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  z-index: 1;
}

.search-viewport-conteiner {
  display: flex;
  flex-direction: column;
}

.search-viewport__nav-conteiner {
  margin: 0.5rem 0rem 0.5rem 0rem;
}

.search-viewport__nav-conteiner h4 {
  margin: 0;
