}

.main {
  width: 100%;
  background-color: #F2F0F1;
}

.grid-1 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.grid-3 article:nth-child(1) {
  grid-column: 1/5;
}

.grid-3 article:nth-child(2) {
  grid-column: 1/3;
}

.grid-3 article:nth-child(3) {
  grid-column: 3/5;
}

.grid-3i {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.grid-3i article:nth-child(1) {
  grid-column: 1/3;
}

.grid-3i article:nth-child(2) {
  grid-column: 3/5;
}

.grid-3i article:nth-child(3) {
  grid-column: 1/5;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.product-card {
  width: 100%;
  max-width: 100%;
}

.product-card__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.product-card__text__titulo h4 {
  margin: 0;
  padding: 0;
  text-align: left;
  word-break: break-word;
}

.product-card__text__precio {
  display: flex;
  flex-direction: column;
}

.product-card__text__descripcion-p {
  margin: 0.8rem 0.5rem 0.8rem 0.4rem;
}

.promo-conteiner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
}

.promo, .importe {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  padding: 0rem 0.5rem 0rem 0.5rem;
  margin: 0rem 0rem 0.5rem 0rem;
}

.promo {
  background-color: rgb(225, 120, 120);
  border-radius: 1.2rem;
}

.promo__porcentaje {
  font-size: 2rem;
  padding: 0;
  margin: 0;
}

.importe__precio {
  font-size: 3.2rem;
  margin: 0;
  padding-top: 0.8rem;
}

.precio-lista {
  display: flex;
  flex-direction: row;
  justify-self: left;
  align-items: center;
  font-size: 1.6rem;
  padding-bottom: 1rem;
}

.precio-lista__precio {
  margin: 0;
  font-size: 1.4rem;
  text-decoration: line-through;
}

.section {
  background-color: #FFFFFF;
  margin-bottom: 1rem;
  margin-top: 1rem;
}

/*Slider*/
.section__slider-ui {
  overflow: hidden;
  width: 100%;
}

.slider-ui__slider {
  display: flex;
  flex-direction: row;
  width: 400%;
  animation: slider 16s infinite ease-in-out;
}

.slider-ui__slider picture {
  width: 100%;
}

@keyframes slider {
  0% {
    margin-left: 0%;
  }
  20% {
    margin-left: 0%;
  }
  25% {
    margin-left: -100%;
  }
  45% {
    margin-left: -100%;
  }
  50% {
    margin-left: -200%;
  }
  70% {
    margin-left: -200%;
  }
  75% {
    margin-left: -300%;
  }
  95% {
    margin-left: -300%;
  }
  100% {
    margin-left: 0%;
  }
}
